import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FlatListComponent } from './flat-list.component';
import { FlatListRoutingModule } from './flat-list-routing.module';
import { MatInputModule, MatFormFieldModule, MatTableModule, MatPaginatorModule,
  MatSortModule, MatCardModule, MatCheckboxModule, MatTooltipModule, MatSlideToggleModule} from '@angular/material';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import {ToasterModule } from 'angular5-toaster';


@NgModule({
  imports: [
    CommonModule,
    FlatListRoutingModule,
    FormsModule,
    ReactiveFormsModule,
    MatFormFieldModule,
    MatCardModule,
    MatInputModule,
    MatCheckboxModule,
    MatPaginatorModule,
    MatTableModule,
    MatSortModule,
    MatTooltipModule,
    MatSlideToggleModule,
    ToasterModule
  ],
  declarations: [FlatListComponent]
})
export class FlatListModule { }



